/*-----------обнуление---------------*/
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
ul,
ol,
li {
  list-style: none;
}
img {
  vertical-align: top;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

html,
body {
  height: 100%;
  font-size: 16px;
  color: #131010;
}

.wrapper {
  min-height: 100%;
  overflow: hidden;
  /*width: 1440px;*/
  margin: 10px 120px 0px 120px;
}

/*-----header-------------------*/
.header {
  width: 100%;
  padding: 0;
  margin: 0 auto;
}

.hopening-time {
  display: flex;
  width: 100%;
  padding: 14px 290px;
  justify-content: center;
  background: #35b8be;
}
.header_container {
  min-height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 683px;
  width: 100%;
}

.header_menu.menu {
  display: flex;
  justify-items: center;
  align-items: center;
}
.menu_list {
  display: flex;
  height: 60px;
}
.menu_item {
  padding: 20px 16px;
}
.menu_link {
  color: #28224b;
  font-family: "Inter", sans-serif;
  font-size: 15px;
}
.menu_link:hover {
  color: #35b8be;
}

.menu_link.sixteen {
  color: #28224b;
  font-size: 16px;
  font-family: "Inter", sans-serif;
}
.menu_link.sixteen:hover {
  color: #35b8be;
}
.menu_item.color {
  position: relative;
  background: url("../Layout\ 8/pictures/main-color-button.png"), #35b8be;
  width: 55px;
  height: 55px;
}
.menu_link.number {
  position: absolute;
  top: 0px;
  left: 30px;
  margin: -6px -6px 6px 6px;
  width: 25px;
  height: 25px;
  border-radius: 100px;
  background: #fff;
  text-align: center;
  box-shadow: 7px 7px 25px 0px rgba(126, 130, 143, 0.27);
  padding: 5px 8px;
  color: #35b8be;
  font-family: "Inter", sans-serif;
  font-size: 13px;
}
.menu_list > .menu_item.color {
  margin-left: 14px;
}

/*----------main----------------*/
.central {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 100px 120px 0px 120px;
  position: relative;
}
.central_main {
}

.main-left {
  width: 100%;
  height: 494px;
  padding-top: 14px;
  z-index: 3;
}
.main-blok {
}
.main-blok__title {
  color: #08090a;
  font-family: "Inter", sans-serif;
  font-size: 60px;
  line-height: 60px;
  letter-spacing: 1.8px;
}
span.color-text {
  color: #35b8be;
  font-family: "Inter", sans-serif;
  font-size: 60px;
  line-height: 60px;
  letter-spacing: 1.8px;
}
.main-blok__text {
  color: #546285;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  line-height: 24.12px;
  letter-spacing: 0.36px;
  padding-top: 27px;
}
.main-blok__button {
  width: 193px;
  height: 60px;
  background: url("../Layout\ 8/pictures/main-color-button.png");
  display: inline-block;
  margin-top: 53px;
  padding: 20px 35px;
}
a.button-two {
  color: #fff;
  font-family: "Inter", sans-serif;
  font-size: 17px;
}
.main-blok__logotype {
  width: 282px;
  margin-top: 30px;
}
.Logotype__picture {
  width: 110px;
  height: 27px;
}
.main-blok__reviews {
  margin-top: 10px;
  display: flex;
}
.main-blok__reviews-rating {
  color: #35b8be;
  width: 90px;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  display: inline-block;
}
.main-blok__reviews-reviews {
  color: #08090a;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  width: 192px;
}
.main-right {
  width: 600px;
  height: 580px;
  z-index: 3;
}
._ibg {
  position: relative;
}
._ibg img {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
.main-blok__image {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
@media screen and (max-width: 1440px) {
  .wrapper {
    min-height: 100%;
    margin: 10px 60px 0px 60px;
  }

  .header_container {
    gap: 383px;
  }
  .central {
    padding: 60px 15px 0px 15px;
  }
  .margin-left {
    padding: 0;
  }
  .main-blok__button {
    margin-top: 23px;
  }
  .main-blok__title {
    font-size: 50px;
    line-height: 60px;
    letter-spacing: 1.4px;
  }
  span.color-text {
    font-size: 50px;
    line-height: 60px;
    letter-spacing: 1.4px;
  }
  .main-blok__logotype {
    margin-top: 15px;
  }
  .main-right {
    margin-left: 80px;
  }
}
@media screen and (max-width: 1150px) {
  .wrapper {
    min-height: 100%;
    margin: 10px 30px 0px 30px;
  }

  .header_container {
    gap: 83px;
    margin: 0 auto;
  }
  .central {
    display: block;
    padding: 15px;
  }
  .main-body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }

  .main-left {
    position: relative;
    text-align: center;
  }

  .main-right {
    display: none;
  }
  .main-blok__button {
    margin: 0px;
  }
  .main-blok__logotype {
    display: inline-block;
    margin: 0px;
  }
}
@media screen and (max-width: 970px) {
  .header_container {
    gap: 13px;
  }
}
@media screen and (max-width: 768px) {
  .main-blok__title {
    font-size: 40px;
    line-height: 50px;
    letter-spacing: 1.2px;
  }
  span.color-text {
    font-size: 40px;
    line-height: 50px;
    letter-spacing: 1.2px;
  }
  .main-blok__text {
    font-size: 16px;
  }
  .wrapper {
    margin: 0 auto;
    padding: 5px;
  }
}
@media screen and (max-width: 600px) {
  .central {
    padding: 5px;
  }

  .hopening-time {
    padding: 14px 0px;
  }
}
@media screen and (max-width: 523px) {
  .main-blok__title {
    font-size: 30px;
    line-height: 40px;
    letter-spacing: 1px;
  }
  span.color-text {
    font-size: 30px;
    line-height: 40px;
    letter-spacing: 1px;
  }
  .main-blok__text {
    font-size: 15px;

    padding-top: 17px;
  }
  .hopening-time {
    padding: 14px 0px;
  }
  .menu_list {
    display: none;
  }
  .header_container {
    gap: 70%;
    position: relative;
  }
}
@media screen and (max-width: 523px) {
  .hopening-time {
    display: none;
  }
  .main-blok__image {
    display: none;
  }
  .wrapper {
    background: #f5fbfc;
  }
  .main-body {
    flex-direction: column;
    align-items: flex-start;
  }
  .main-blok__logotype {
    margin-top: 10px;
  }
  .main-blok__reviews-rating {
    font-size: 14px;
  }
  .main-blok__reviews-reviews {
    font-size: 14px;
  }
}
